<script>
import { request } from '@/utils/request'
import { useUserStore } from '@/stores/user'

export default {
  data() {
    return {
      payMethod: 'wechat',
      orderInfo: {
        title: 'ËáªÊâòÂπ≥Âè∞UIÂéüÂûãËÆæËÆ°È°πÁõÆ',
        amount: '3,500.00',
        deliveryDays: 7
      }
    }
  },
  methods: {
    goBack() {
      uni.navigateBack()
    },
    async handleSubmit() {
      const userStore = useUserStore()
      if (!userStore.checkLogin()) {
        uni.showToast({ title: 'ËØ∑ÂÖàÁôªÂΩï', icon: 'none' })
        setTimeout(() => {
          uni.navigateTo({ url: '/pages/login/index' })
        }, 1500)
        return
      }
      
      uni.showLoading({ title: 'Êèê‰∫§‰∏≠...' })
      
      try {
        // ÂàõÂª∫ËÆ¢Âçï
        const createRes = await request({
          url: '/order/create',
          method: 'POST',
          data: {
            title: this.orderInfo.title,
            description: 'ËÆ¢ÂçïÊèèËø∞',
            categoryId: 5,
            deliveryTime: new Date(Date.now() + this.orderInfo.deliveryDays * 24 * 60 * 60 * 1000).toISOString().split('T')[0],
            stages: [{
              name: 'ÂÖ®Ê¨æ',
              amount: parseFloat(this.orderInfo.amount.replace(/,/g, '')),
              percent: 100,
              milestone: 'ÂÆåÊàê‰∫§‰ªò'
            }]
          }
        })
        
        if (createRes.data && createRes.data.id) {
          // ÊâòÁÆ°ËµÑÈáë
          await request({
            url: `/order/${createRes.data.id}/deposit`,
            method: 'POST'
          })
          
          uni.hideLoading()
          uni.showToast({ title: 'ËÆ¢ÂçïÂàõÂª∫ÊàêÂäü', icon: 'success' })
          setTimeout(() => {
            uni.reLaunch({ url: '/pages/index/index' })
          }, 1500)
        }
      } catch (e) {
        uni.hideLoading()
        uni.showToast({ title: e.message || 'ÂàõÂª∫Â§±Ë¥•', icon: 'none' })
      }
    }
  }
